.clock {
  width: 550px;
  height: 550px;
  background-image: url("clock.7b51b2d2.jpg");
  background-size: cover;
  border: 1px #8b4513;
  border-radius: 50%;
  position: relative;
  top: 30%;
  left: 40%;
}

.clock-arrow {
  transform-origin: bottom;
  background-color: #000;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  position: absolute;
  transform: translateX(-50%);
}

.clock-center {
  width: 20px;
  height: 20px;
  background-color: #000;
  border-radius: 50%;
  position: absolute;
  top: 268px;
  left: 268px;
}

.clock-seconds__arrow {
  width: 5px;
  height: 40%;
  bottom: 50%;
  left: 50%;
}

.clock-minutes__arrow {
  width: 7px;
  height: 33%;
  bottom: 50%;
  left: 50%;
}

.clock-hours__arrow {
  width: 12px;
  height: 24%;
  bottom: 50%;
  left: 50%;
}

